@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 300;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: 900;
}

:root {
  --font-color: #3d4e61;
}

html {
  font-family: Avenir, sans-serif;
}

body {
  margin-top: 0;
}

.inputs {
  flex-direction: column;
  margin-top: 20px;
  display: flex;
}

.input__search {
  color: var(--font-color);
  box-sizing: border-box;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  width: 100%;
  font-family: inherit;
  box-shadow: 0 1px 8px #0000001a;
}

.inputs--top {
  background-image: url("Search.ccd3c07b.svg");
  background-position: 26px;
  background-repeat: no-repeat;
  background-size: 19px;
  height: 70px;
  padding-left: 62px;
  font-size: 16px;
}

.inputs--bottom {
  background-image: url("Search.ccd3c07b.svg");
  background-position: 13px;
  background-repeat: no-repeat;
  background-size: 11px;
  height: 42px;
  padding-left: 33px;
  font-size: 14px;
}

.inputs--top:hover, .inputs--bottom:hover {
  box-shadow: 0 2px 2px #0003;
}

.inputs--top:focus, .inputs--bottom:focus {
  text-shadow: 0 4px 4px #0003;
  background-color: #f6f6f7;
  border: 1px solid #e1e7ed;
  outline: none;
  font-weight: 900;
  box-shadow: 0 4px 4px #00000040;
}

.input__search::placeholder {
  color: var(--font-color);
  font-family: inherit;
  font-size: 16px;
  font-weight: 300;
}

.inputs--bottom::placeholder {
  color: var(--font-color);
  font-family: inherit;
  font-size: 14px;
  font-weight: 300;
}
